import { Dispatch } from "react";
import { AppActions } from "../_types";
import {
  REQUEST_PLAYER,
  RESIZE_PLAYER,
  RESET_PLAYER,
  DRAW_FINGER,
  DRAW_GAME_PIANO,
  DRAW_NOTES,
  DRAW_GAME,
  ICanvas,
  ENTER_PLAYMODE,
  EXIT_PLAYMODE,
  DRAW_SEEKBAR,
  DRAW_NOTE_PIANO,
  INoteDraw,
} from "../_types/player.types";
import { store } from "../_store/store.config";
import { API, AudioApi } from "../_helpers/api";
import MidiState from "../_helpers/midiContext";
import { sizeCalculator } from "../_helpers/pianoSizes";
import { Melodition } from "../plugin/Melodition/Melodition";
import { ILesson } from "../_types/lesson.types";
import { addPlayTimes } from "./profile.actions";
import { cauntMeasures } from "../_helpers/constants";

var Melo: Melodition | undefined;

export const requestPlayer = (info: ILesson) => (
  dispatch: Dispatch<AppActions>
) => {
  import("../_helpers/playerApi").then(({ lessonCompiler }) => {
    lessonCompiler(info)
      .then((Melodi) => {
        if (Melodi) {
          if (Melodi.songs.length > 0) {
            Melodi.songs.forEach((song, index) =>
              API.get(`/song/v2/${song.id}`)
                .then(({ data }) =>
                  AudioApi(data)
                    .then((res) => {
                      Melodi.song
                        ?.loadBuffer({ buffer: res.data, vol: song.vol })
                        .then(() => {
                          if (Melodi.songs.length === index + 1) {
                            const realTempo = Melodi.tempo / 60;
                            const songbeat =
                              Melodi.song?.getDuration()! / realTempo;
                            newMelodition(Melodi, songbeat);
                          }
                        });
                    })
                    .catch((bufferError) => console.log(bufferError))
                )
                .catch((urlError) => console.log(urlError))
            );
          } else {
            newMelodition(Melodi);
          }
        }
      })
      .catch((err) => console.log(err));
  });
  function newMelodition(Melodi: any, songbeat?: number) {
    Melodi.songLength = cauntMeasures(
      Melodi.rythm,
      Melodi.storeDrums,
      Melodi.storeTracks,
      songbeat
    );
    Melodi.loaded = true;
    Melo = new Melodition(Melodi);
    const { songLength, neededLines, neededTracks, bpm } = Melodi;
    dispatch({
      type: REQUEST_PLAYER,
      payload: { songLength, neededLines, neededTracks, bpm },
    });
  }
};

export const gamePiano = (
  keyRefs: SVGGElement[],
  canvasSize?: ICanvas,
  tapSize?: number
) => (dispatch: Dispatch<AppActions>) => {
  if (canvasSize && tapSize) {
    const pianoSize = sizeCalculator(keyRefs, canvasSize);
    Melo?.assigner({ pianoSize, keyRefs, canvasSize, tapSize });
    dispatch({
      type: DRAW_GAME_PIANO,
      payload: { pianoSize, canvasSize, tapSize },
    });
  } else {
    Melo?.assigner({ keyRefs });
    dispatch({ type: DRAW_GAME_PIANO });
  }
};
export const notePiano = (keyRefs: SVGGElement[], noteInfo: INoteDraw) => (
  dispatch: Dispatch<AppActions>
) => {
  Melo?.assigner({ noteInfo, keyRefs });
  dispatch({ type: DRAW_NOTE_PIANO, payload: { noteInfo } });
};
export const drawFinger = (payload: any) => (
  dispatch: Dispatch<AppActions>
) => {
  Melo?.assigner(payload);
  if (!Melo?.playMode) Melo?.stopReset();
  dispatch({ type: DRAW_FINGER });
};
export const drawSeekBar = (payload: any) => (
  dispatch: Dispatch<AppActions>
) => {
  Melo?.assigner(payload);
  dispatch({ type: DRAW_SEEKBAR });
};
export const drawGame = (payload: any) => (dispatch: Dispatch<AppActions>) => {
  Melo?.drawGame(payload);
  dispatch({ type: DRAW_GAME });
};
export const drawNotes = (payload: any) => (dispatch: Dispatch<AppActions>) => {
  Melo?.drawNote(payload);
  dispatch({ type: DRAW_NOTES });
};
export const resizePlayer = () => (dispatch: Dispatch<AppActions>) => {
  Melo?.stopPlay();
  dispatch({ type: RESIZE_PLAYER });
};
export const resetPlayer = () => (dispatch: Dispatch<AppActions>) => {
  Melo?.stopPlay();
  dispatch({ type: RESET_PLAYER });
};
export const changeMode = (mode: boolean) => (
  dispatch: Dispatch<AppActions>
) => {
  Melo?.assigner({ playMode: mode });
  Melo?.stopPlay();
  if (mode) dispatch({ type: ENTER_PLAYMODE });
  else dispatch({ type: EXIT_PLAYMODE });
};

export const canvasWheel = (e: number | React.WheelEvent) => {
  Melo?.rakeMouseWheel(e);
};
export const startPlayer = () => {
  Melo?.startPlay();
};
export const stopPlayer = () => {
  Melo?.stopPlay();
  const score = MidiState.score;
  if (Melo && score.length > 0) {
    const details = Melo.songDetails;
    const len = Melo.neededTracks.length;
    const progress = Math.round((score.length / len) * 100) + "%";
    const saveData = {
      song: { name: details.title, id: details.id, notes: len },
      time: new Date().toLocaleTimeString(),
      score: JSON.stringify(score),
      date: new Date().toLocaleDateString(),
      progress,
    };
    MidiState.score = [];
    store.dispatch(addPlayTimes(saveData));
  }
};

export const midiNoteOn = (pitch: number, vol = 0.7) => {
  Melo?.midiNoteOn(pitch, vol);
};
export const midiNoteOff = (pitch: number) => {
  Melo?.midiNoteOff(pitch);
};
