import { ICanvas, IPianoSizes } from "../_types/player.types";

export const sizeCalculator = (keyRefs: SVGGElement[], pianoSize: ICanvas) => {
    const lineWidth =
        keyRefs[50].getBoundingClientRect().x -
        (keyRefs[48].getBoundingClientRect().x +
            keyRefs[48].getBoundingClientRect().width);
    const keyXPos = pianoSize.pianoLeft
    const padLeft = keyXPos ? keyXPos : 100;
    const notePos: IPianoSizes[] = []
    for (let k in keyRefs) {
        const key = keyRefs[k];
        const rect = key.getBoundingClientRect();
        const x = rect.x - padLeft;
        const w = rect.width;
        const centerX = x + w / 2;
        if (key.classList.contains("C") || key.classList.contains("F")) {
            notePos.push({ left: x, center: centerX - lineWidth * 3.5, width: w, key: "CF" });
        } else if (key.classList.contains("last")) {
            notePos.push({ left: x, center: centerX, width: w, key: "last" });
        } else if (key.id === "black") {
            notePos.push({ left: x, center: centerX, width: w, key: "black" });
        } else if (key.classList.contains("E") || key.classList.contains("B"))
            notePos.push({ left: x, center: centerX + lineWidth * 3.5, width: w, key: "EB" });
        else if (key.classList.contains("A"))
            notePos.push({ left: x, center: centerX + lineWidth * 1.8, width: w, key: "A" });
        else if (key.classList.contains("G"))
            notePos.push({ left: x, center: centerX + lineWidth * 1.4, width: w, key: "G" });
        else notePos.push({ left: x, center: centerX, width: w, key: "white" });
    }
    notePos.push({ left: -100, center: 0, width: lineWidth, key: "lineWidth" });
    return notePos;
}