interface IFingersClass {
  note: any,
  len: number,
  hand: any,
  beat: number
  num: number
  pitch: number
}
export class FingersClass {
  len: number;
  note: any;
  finger: SVGElement;
  hand: string;
  color: string;
  bColor: string;
  fColor: string;
  cColor: string;
  end: number;
  num: number;
  pitch: number;
  showing: boolean;
  constructor({ note, len, hand, beat, num, pitch }: IFingersClass) {
    this.len = len;
    this.note = note;
    this.num = num;
    this.pitch = pitch;
    this.end = this.len - 1 + beat;
    if (this.end <= beat) {
      this.end = beat + 1;
    }
    this.hand = hand.hand;
    this.finger = hand.finger;
    this.fColor = "rgba(210, 210, 210, 0.5)";/*hand.finger.getAttribute("fill");*/
    this.cColor = note.children.front.getAttribute("fill");
    this.bColor = note.id;
    if (this.hand === "right") {
      this.color = "yellow";
    } else {
      this.color = "skyblue";
    }
    this.showing = true;
  }
  show(c?: string) {
    let color = this.color;
    if (c) color = c
    this.note.children.back.setAttribute("fill", color);
    this.note.children.front.setAttribute("fill", "black");
    this.finger.setAttribute("fill", color);
  }
  hide() {
    this.note.children.back.setAttribute("fill", this.bColor);
    this.note.children.front.setAttribute("fill", this.cColor);
    this.note.children.back.style.fill = "";
    this.note.children.front.style.fill = "";
    this.finger.setAttribute("fill", this.fColor);
    this.showing = false
  }
}
export class BackMover {
  public y: number;
  public end: number;
  private yPos: number;
  public margin: number;
  constructor(beat: number, height: number, margin: number) {
    this.y = 0;
    this.margin = margin;
    this.yPos = beat * height;
    this.end = this.yPos + height + margin;
  }
  update = (ctx: CanvasRenderingContext2D, img: HTMLImageElement) => {
    ctx.drawImage(img, 0, Math.floor(this.y - this.yPos - this.margin));
  }
}
interface IMusicLine {
  x: number;
  y: number;
  lw: number;
  color: string;
  width: number;
}
export class MusicLine {
  private x: number;
  private y: number;
  private lw: number;
  private color: string;
  private width: number;
  constructor({ width, color, x, y, lw }: IMusicLine) {
    this.x = x;
    this.y = y;
    this.width = width;
    this.color = color;
    this.lw = lw
  }
  draw = (ctx: CanvasRenderingContext2D) => {
    ctx.beginPath();
    ctx.moveTo(0, this.y);
    ctx.lineTo(this.width, this.y);
    ctx.lineWidth = this.lw
    ctx.strokeStyle = this.color
    ctx.stroke();
    ctx.fill();
  }
}
interface ILessonNote {
  width: number,
  height: number,
  color: string,
  x: number,
  note: number;
  y: number,
  end: number,
  text: string,
  gap: number,
}
export class LessonNote {
  y: number;
  x: number;
  r: number;
  gap: number;
  beg: number;
  end: number;
  start: number;
  height: number;
  width: number;
  mover: number;
  note: number;
  text: string;
  color: string;
  lineColor: string;
  crash: boolean;
  hit?: number;
  constructor({ width, height, color, x, y, end, note, text, gap }: ILessonNote) {
    this.y = y;
    this.start = -y;
    this.gap = gap;
    this.beg = y + gap;
    this.end = end;
    this.note = note;
    this.height = -height;
    this.x = x;
    this.text = text;
    this.width = width;
    this.r = width / 2;
    this.color = color;
    this.lineColor = 'black';
    this.mover = height;
    this.crash = false;
  }
  move = () => {
    this.crash = true;
    this.mover = this.y - this.gap;
    if (this.mover > -this.height) {
      this.mover = -this.height
    }
  }
  update = (ctx: CanvasRenderingContext2D) => {
    ctx.beginPath();
    ctx.moveTo(this.x, this.y + this.start);
    ctx.lineTo(this.x, this.y + this.height);
    ctx.lineWidth = this.width * 0.85;
    ctx.strokeStyle = 'black';
    ctx.lineCap = 'round';
    ctx.stroke();
    ctx.beginPath();
    ctx.moveTo(this.x, this.y + this.start);
    ctx.lineTo(this.x, this.y + this.height);
    ctx.lineWidth = this.width * 0.2;
    ctx.strokeStyle = 'white';
    ctx.stroke();
    if (this.crash) {
      ctx.beginPath();
      ctx.moveTo(this.x, this.y + this.start);
      ctx.lineTo(this.x, this.y - this.mover);
      ctx.lineWidth = this.width * 0.2;
      ctx.strokeStyle = 'red';
      ctx.stroke();
    }
    if (this.hit) {
      ctx.beginPath();
      ctx.moveTo(this.x, this.y + this.start);
      ctx.lineTo(this.x, this.y - this.hit);
      ctx.lineWidth = this.width * 0.25;
      ctx.strokeStyle = this.color;
      ctx.stroke();
    }
    if (this.crash && !this.hit) {
      this.color = 'red';
    }
    ctx.beginPath();
    ctx.arc(this.x, this.y + this.start, this.r * 1.4, 0, 2 * Math.PI);
    ctx.fillStyle = 'black';
    ctx.fill()
    ctx.beginPath();
    ctx.arc(this.x, this.y + this.start, this.r, 0, 2 * Math.PI);
    ctx.fillStyle = this.color;
    ctx.fill()
    ctx.font = 'bold ' + this.r * 1.3 + 'px arial';
    ctx.textAlign = 'center';
    ctx.textBaseline = 'middle';
    ctx.fillStyle = 'black';
    ctx.fillText(this.text, this.x, this.y + this.start + 1);
  }
}
interface IExerciseNote {
  x: number,
  y: number,
  r: number,
  note: string;
  pitch: number;
  text?: string;
  outline?: OutLine;
}
export class ExerciseNote {
  x: number;
  y: number;
  r: number;
  note: string;
  text?: string;
  pitch: number;
  color: string;
  outline?: IOutLineProps;
  constructor(props: Partial<IExerciseNote>) {
    Object.assign(this, props);
    this.color = "white"
  }
  update = (ctx: CanvasRenderingContext2D) => {
    ctx.beginPath();
    ctx.arc(this.x, this.y, this.r, 0, 2 * Math.PI);
    ctx.fillStyle = "rgba(0, 0, 0, 0.2)";
    ctx.fill();
    ctx.beginPath();
    ctx.arc(this.x, this.y, this.r * 0.6, 0, 2 * Math.PI);
    ctx.fillStyle = this.color;
    ctx.fill();
    if (this.text) {
      ctx.font = this.r * 2 + 'px arial';
      ctx.textAlign = 'center';
      ctx.textBaseline = 'middle';
      ctx.fillStyle = "black";
      ctx.fillText(this.text, this.x - this.r * 1.2, this.y);
    }
    if (this.outline) {
      ctx.globalCompositeOperation = 'destination-over';
      new OutLine(ctx, this.outline)
      ctx.globalCompositeOperation = 'source-over';
    }
  }
}
export interface IOutLineProps {
  r: number,
  x: number,
  y: number
}
export class OutLine {
  constructor(ctx: CanvasRenderingContext2D, { r, x, y }: IOutLineProps) {
    ctx.beginPath();
    ctx.moveTo(x - r, y);
    ctx.lineTo(x + r, y);
    ctx.lineWidth = 0.5;
    ctx.strokeStyle = "black";
    ctx.stroke();
  }
}
