import { midiNoteOff, midiNoteOn } from "./player.actions";
import { errorHandler, seccessHandler } from "../components/Layout/SnackBar/alert";

let midiInput: WebMidi.MIDIInput;

export const requsetMidi = () => {
    if (!navigator.requestMIDIAccess) errorHandler("لطفاً برای استفاده کامل از امکانات سایت از گوگل کروم یا مایکروسافت اج استفاده کنید.");
    else navigator.requestMIDIAccess()
        .then((midiAccess) => {
            console.log("MIDI Ready!");
            for (let entry of midiAccess.inputs) {
                midiInput = entry[1]
                seccessHandler("MIDI input device: " + midiInput.name)
                console.log(midiInput)
                midiInput.onmidimessage = onMidiMessage;
                midiInput.onstatechange = onstatechange;
            }
        })
        .catch((error) => {
            errorHandler("Error accessing MIDI devices: " + error);
        });
}
export const changeMidi = (enable: boolean) => {
    if (midiInput)
        midiInput.onmidimessage = enable ? onMidiMessage : () => { };
}

// const noteNames: string[] = ["C", "C#", "D", "D#", "E", "F", "F#", "G", "G#", "A", "A#", "B"];

const onstatechange = (midiState: WebMidi.MIDIConnectionEvent): void => {
    console.log(midiState)
    // warningHandler(midiState.)
}

let notes: number[] = [];
let hold = false;

const onMidiMessage = (midiEvent: WebMidi.MIDIMessageEvent): void => {
    let data: Uint8Array = midiEvent.data;
    if (data.length === 3) {
        // status is the first byte.
        let status = data[0];
        // command is the four most significant bits of the status byte.
        let command = status >>> 4;
        // channel 0-15 is the lower four bits.
        // let channel = status & 0xF;

        if (command === 11) {
            let velocity = data[2];
            if (velocity === 127 || velocity === 0)
                hold = holder(velocity)
        }
        // just look at note on and note off messages.
        if (command === 0x9 || command === 0x8) {
            // note number is the second byte.
            let note = data[1];
            // velocity is the thrid byte.
            let velocity = data[2];

            // let commandName = command === 0x9 ? "Note On " : "Note Off";
            // calculate octave and note name.
            // let noteName = noteNames[note % 12];
            // let octave = Math.trunc(note / 12);

            if (command === 0x9 && velocity) {
                midiNoteOn(note, velocity / 100)
                notes.push(note)
            } else {
                if (!hold) {
                    midiNoteOff(note)
                    notes = [];
                }
            }
            // console.log(`${commandName} ${noteName}${octave} ${velocity}`);
        }
    }
}
const holder = (vol: number) => {
    if (vol === 0) {
        notes.forEach(n => {
            midiNoteOff(n)
        })
        notes = []
        return false
    } else {
        return true
    }
}